import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Row, Col } from 'antd';
import {PalavraAzul} from './Texts';


const MolduraPort = styled.div`
width: 85%;
height: 20vh;
border-radius: 5px;
background: ${({ theme }) => theme.colors.white};
overflow: hidden;
margin-top: ${props => props.margintop};
margin-left: ${props => props.marginleft};
img {
    height:100%;
    width: auto;
}
:hover {
    box-shadow: 0px 0px 5px 1px ${({theme}) => theme.colors.boxshadowescuro};
}
@media (max-width: 768px) {
    height: 100px;
    z-index: 3;
    margin-top: 10%;
    img {
    height:auto;
    width: 100%;
    margin-top: -25%;
    transform: translateY(25%)
    }
}
@media (max-width: 568px) {
    height: 80px;
    z-index: 3;
    margin-top: 10%;
    background: ${({theme}) => theme.colors.purple};
    img {
    width: auto;
    height: 100%;
    margin-left: -33%;
    transform: translateY(33.5%);
    }
}
`
const Carouselcard = styled.div`
width: 90%;
height: 60vh;
margin-top: 10%;
margin-left: 10%;
border-radius: 10px;
overflow: hidden;
img {
    height:100%;
    width: 100%;
}
@media (max-width: 768px) {
    margin-top: 0;
    width: 90%;
    height: 300px;
    img {
    height:100%;
    }
}
@media (max-width: 568px) {
    height: 220px;
    img {
    height:100%;
    }
}
`
const Links =styled.h2`
padding-left: 10%;
font-size: 1.5vw;
color: ${({theme}) => theme.colors.yellow};
a {
    text-decoration: none;
    color: ${({theme}) => theme.colors.yellow};
}
`
const Base = "../static/Cantinhodasflores.png"

export function Portfolio(props) {
    const [photo, setPhoto] = useState("")
    const [link, setLink] = useState("")

    return (
        <Row>
            <Col span={24}>
                <Row>
                    <Col lg={12} md={22} sm={22} xs={22}>
                        <Row>
                            <Col span={8}>
                                <MolduraPort marginleft="25%" margintop="30%" onClick={() => setPhoto("../static/Cantinhodasflores.png")} >
                                    <img src="..\static\Cantinhodasflores.png" />
                                </MolduraPort>
                            </Col>
                            <Col span={8}>
                                <MolduraPort marginleft="20%" margintop="30%" onClick={() => setPhoto("../static/Pokedex.png")}>
                                    <img src="..\static\Pokedex.png" />
                                </MolduraPort>
                            </Col>
                            <Col span={8}>
                                <MolduraPort marginleft="15%" margintop="30%" onClick={() => setPhoto("../static/Dashboard.jpg")}>
                                    <img src="..\static\Dashboard.jpg" />
                                </MolduraPort>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={8}>
                                <MolduraPort marginleft="25%" margintop="10%" onClick={() => setPhoto("../static/Graphic.jpg")}>
                                    <img src="..\static\Graphic.jpg" />
                                </MolduraPort>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={24}>
                                <PalavraAzul word1="Em construção..."/>
                            </Col>
                        </Row>
                    </Col>
                    <Col lg={12} md={22} sm={22} xs={22}>
                        <Carouselcard >
                            <img src={photo || Base}/>
                        </Carouselcard>
                        <Links><a href={link}>Veja o projeto! Clique aqui.</a></Links>
                    </Col>
                </Row>
            </Col>
        </Row>
    )
}